<style>
  .order-id {
    cursor: pointer;
    color: #2077FF;
  }
  .order-id:hover {
    color: #409EFF;
  }
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <region-city-store @StoreGroupChange="StoreGroupChange" :showGroup="false" :showStore="false" :defaultCityId="searchContent.cityId"></region-city-store>
        <el-form-item label="拼团号">
          <el-input class="search-input" v-model.trim="searchContent.groupNo" placeholder="请输入拼团号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="订单号">
          <el-input class="search-input" v-model.trim="searchContent.orderNo" placeholder="请输入订单号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="拼团商品">
          <el-input class="search-input" v-model.trim="searchContent.skuNoOrGoodsName" placeholder="商品名称关键字" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="开团时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRange"
            format="yyyy-MM-dd"
            type="daterange"
            @datePickerRangeChange="datePickerRangeChange">
          </date-picker-range>
        </el-form-item>
        <el-form-item label="拼团状态">
          <el-select class="search-input" v-model="searchContent.groupStatus" filterable placeholder="请选择业务类型" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in groupStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>  
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        ref="multipleTable"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="140px" label="拼团编号">
          <template slot-scope="scope" size="small">
            <p class="order-id" @click="showDetail(scope.row)" >{{scope.row.groupNo}}</p>
          </template>
        </el-table-column>
        <el-table-column min-width="80px" prop="joinLimitNum" label="几人团"></el-table-column>
        <el-table-column min-width="80px" prop="groupJoinedNum" label="已成团"></el-table-column>
        <el-table-column min-width="110px" prop="lastFor" label="成团时长(分)"></el-table-column>
        <el-table-column min-width="180px" prop="createTime" label="开团时间"></el-table-column>
        <el-table-column min-width="120px" prop="groupStatus" label="拼团状态" :formatter="groupStatusFormater"></el-table-column>
        <el-table-column min-width="120px" prop="groupCreateMemberName" label="发起人"></el-table-column>
        <el-table-column min-width="120px" prop="groupCreateMemberPhone" label="发起人手机"></el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import config from '@/utils/config'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore';
  const requestUrl = {
    groupList: '/member/zt/group/groupList'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes20,
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        tableData: [],
        searchContent: {
          regionCode: Storage.get('regionCode') || '',
          cityId: '',
          source: 'web',
          defaultRange: [],
          createTimeEnd: '', // 开团结束时间，格式：yyyy-MM-dd
          createTimeStart: '', // 开团开始时间，格式：yyyy-MM-dd
          groupNo: '', // 拼团号
          groupStatus: '', // 拼团状态 10-待成团；20-已成团；30-拼团失败
          orderNo: '', // 订单号
          skuNoOrGoodsName: '' // 拼团商品-ERP编码或者商品名称
        },
        groupStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '待成团',
            value: 10
          },
          {
            label: '成团成功',
            value: 20
          },
          {
            label: '成团失败',
            value: 30
          },
          {
            label: '已关闭',
            value: 40
          }
        ]

      }
    },
    mounted() {
      let searchContentMsg = Storage.get("searchContent")
      if(this.$route.meta.keepAlive && searchContentMsg) {
        this.pageNum = searchContentMsg.pageNum
        this.searchContent = searchContentMsg.searchContent
      }
      Storage.remove('searchContent')
      this.doSearchHandel()
    },
    components: {
      "date-picker-range": DatePickerRange,
      "region-city-store": RegionCityStore
    },
    methods: {
      StoreGroupChange(_param) {
        this.searchContent.regionCode = _param.regionCode
        this.searchContent.cityId = _param.cityId
        this.doSearchHandel(true)
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 获取拼团订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) { 
        let beginTimeVal = this.searchContent.createTimeStart ? new Date(this.searchContent.createTimeStart.replace(/-/g, '/')).getTime() : 0
        let endTimeVal = this.searchContent.createTimeEnd ? new Date(this.searchContent.createTimeEnd.replace(/-/g, '/')).getTime() : 0
        
        if(this.searchContent.createTimeStart && this.searchContent.createTimeEnd && !isNaN(beginTimeVal) && !isNaN(endTimeVal)) {
          if(beginTimeVal > endTimeVal) {
            this.$message("开团结束日期不能早于开团开始日期")
            return
          }
        }
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.groupList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize, DIFFCITY: true }).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.list && res.data.list.length) {
              this.tableData = res.data.list
            }else {
              this.tableData = []
            }

            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      // 拼团状态 10-待成团；20-已成团；30-拼团失败
      groupStatusFormater(row, column, cellValue) {
        if(cellValue === 10) {
          return "待成团"
        }else if(cellValue == 20) {
          return "成团成功"
        }else if(cellValue == 30) {
          return "成团失败"
        }else if(cellValue == 40) {
          return "已关闭"
        }else {
          return "--"
        }
      },
      /**
       * [showDetail 查看详情]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showDetail(_row) {
        // 设置搜索条件
        let _paramMesg = {
          searchContent: this.searchContent,
          pageNum: this.pageNum
        }

        Storage.set("searchContent", _paramMesg)
        // 去详情页
        this.$router.push("/group_order/detail/" + _row.groupNo)
      },
      /**
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.createTimeStart = result ? result[0] : ''
        this.searchContent.createTimeEnd = result ? result[1] : ''
        this.doSearchHandel(true)
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }
    
  }
</script>



